<template>
	<view class="box" v-if="type_check">

		<view class="info-li">
			<view class="left">
				<view class="title">
					店铺名称<text class="text-red">*</text>
				</view>
			</view>
			<input v-model="merchantShopName" class="info-li-input" type="text" placeholder="请输入店铺名称">
		</view>

		<view class="info-li all-line">
			<view class="title">
				店铺介绍<text class="text-red">*</text>
			</view>
			<textarea v-model="shopDesc" class="info-li-input info-li-textarea" style="text-align: left;" type="text" maxlength="35"
			 placeholder="请填写店铺介绍(35字以内)"></textarea>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="title">
					店铺微信号<text class="text-red">*</text>
				</view>
			</view>
			<input v-model="shopWechatId" class="info-li-input" type="text" placeholder="请输入微信号">
		</view>

		<view class="info-li">
			<view class="left">
				<view class="title">
					店铺类型<text class="text-red">*</text>
				</view>
			</view>
			<picker v-if="shop_type.length>0" @change="shopChange" :value="shop_index" :range="shop_type" range-key="label">
				<view class="uni-input">{{shopType}}</view>
			</picker>
			<!-- <input v-model="shopType" class="info-li-input" type="text" placeholder="请输入店铺类型"> -->
		</view>
		<view class="info-li all-line" @click="getLocation">
			<view class="title">
				店铺地址<text class="text-red">*</text>
			</view>
			<input v-model="shopAddress" disabled="true" class="info-li-input" style="text-align: left;" type="text" placeholder="请输入详细地址">
		</view>
		<view class="info-li">
			<view class="left">
				<view class="title">
					店长手机号<text class="text-red">*</text>
				</view>
			</view>
			<input maxlength="11" v-model="shopManagerMobile" class="info-li-input" type="text" placeholder="请输入店长手机号">
		</view>
		<view class="info-li">
			<view class="left">
				<view class="title">
					店长姓名<text class="text-red">*</text>
				</view>
			</view>
			<input v-model="shopManagerName" class="info-li-input" type="text" placeholder="请输入店长姓名">
		</view>

		<view class="info-li">
			<view class="left">
				<view class="title">
					营业状态<text class="text-red">*</text>
				</view>
			</view>
			<picker @change="bindPickerChange" :value="status_index" :range="picker_content" range-key="name">
				<view class="uni-input">{{picker_content[status_index].name}}</view>
			</picker>
		</view>
		<view class="info-li all-line">
			<view class="title">
				备注
			</view>
			<input v-model="remarks" class="info-li-input" style="text-align: left;" type="text" placeholder="请输入备注">
		</view>
		<view class="info-li info-photo-li">
			<view class="card-title">
				店铺背景图<text class="text-red">*</text>
			</view>
			<view class="photo_list">
				<view class="photo_li" v-if="!picUrl" @tap="chooseImage('picUrl')">
					<view class="photo_default">
						<image lazy-load class="default_icon" src="../../static/images/photo.png" mode="aspectFill"></image>
						<view class="default_text">
							上传照片
						</view>
					</view>
				</view>
				<view class="photo_li " v-else>
					<image lazy-load class="default_img" :src="pic_url" mode="aspectFill"></image>
					<view class="iconfont iconcuowu" @tap="closeImg('picUrl')"></view>
				</view>

			</view>
		</view>

		<view class="next-btn" @tap='save'>
			提交信息
		</view>

	</view>
	<view class="empty" v-else>
		<image lazy-load class="empty-icon" src="../../static/images/mescroll-empty.png" mode=""></image>
		<view class="empty-tip">
			记录为空
		</view>
	</view>
</template>

<script>
	let seqId = "";
	import {
		mapState
	} from 'vuex';
	export default {
		data() {
			return {
				api_url: this.$webconfig.api_url,
				lon1: 0, //经度
				lat1: 0, //纬度
				shop_type: [],
				shop_index: 0,
				picker_content: [{
					name: '营业中',
					status: 100,
				}, {
					name: '暂停营业',
					status: 99,
				}],
				status_index: 0,
				pic_url: "",
				"enterpriseCreditCode": "",
				"enterpriseExpireDate": "",
				"enterpriseName": "",
				"merchantSeqId": "",
				"merchantShopName": "", //名称
				"picUrl": "", //照片
				"remarks": "", //备注
				"seqId": "",
				"shopAddress": "", //地址
				"shopDesc": "", //介绍
				"shopDetailSeqId": "",
				"shopManagerMobile": "", //店主电话
				"shopManagerName": "", //店主姓名
				"shopMap": "",
				"shopType": "", //类型
				"shopWechatId": "", //店长微信
				"status": 100, //营业状态

			}
		},
		computed: {
			...mapState({
				type_check: state => state.type_check,
			}),
		},
		onLoad(option) {
			let self = this
			this.getType();
			this.merchantSeqId = option.seqId;
			let local_shop = this.$common.getLocalSync('shop_certificate');
			if (local_shop) {
				// 再次入驻 入驻列表进入
				this.enterpriseCreditCode = local_shop.enterpriseCreditCode;
				this.enterpriseExpireDate = local_shop.enterpriseExpireDate;
				this.enterpriseName = local_shop.enterpriseName;
				this.merchantSeqId = local_shop.merchantSeqId;
				this.merchantShopName = local_shop.merchantShopName;
				this.picUrl = local_shop.picUrl;
				this.pic_url = local_shop.picUrl;
				this.remarks = local_shop.remarks;
				this.seqId = local_shop.seqId;
				this.shopAddress = local_shop.shopAddress;
				this.shopDesc = local_shop.shopDesc;
				this.shopDetailSeqId = local_shop.shopDetailSeqId;
				this.shopManagerMobile = local_shop.shopManagerMobile;
				this.shopManagerName = local_shop.shopManagerName;
				this.shopMap = local_shop.shopMap;
				this.shopType = local_shop.shopType;
				this.shopWechatId = local_shop.shopWechatId;
				this.status = local_shop.status;
				this.lon1 = local_shop.lon1;
				this.lat1 = local_shop.lat1;
				if (local_shop.status == 100) {
					this.status_index = 0
				} else {
					this.status_index = 1
				}
				if (!this.lon1 || !this.lat1) {
					self.getNowLocation()
				}
			} else {
				// 直接进入
				self.getNowLocation()
			}
		},
		onUnload() {
			this.$common.removeLocal('shop_certificate')
		},
		methods: {
			getNowLocation() {
				let self = this;
				uni.getLocation({
					type: 'gcj02',
					success: function(res) {
						console.log(res);
						if (!self.lon1 || !self.lat1) {
							self.lon1 = res.longitude
							self.lat1 = res.latitude;
						}
					},
					fail: function(errMsg) {
						console.log(errMsg);
						self.locationFail(errMsg)
					},
				});
			},
			// 获取位置权限  暂时没用
			locationFail(err) {
				let self = this;
				if (err.errMsg === "getLocation:fail auth deny" || err.errMsg === "getLocation:fail authorize no response") { // 没有授权，重新授权，兼容iso和Android
					uni.showModal({
						title: '授权提示',
						content: "是否允许开启位置信息",
						success: (res) => {
							if (res.confirm) { // 点击确定，则调用位置授权
								uni.openSetting({
									success(settingdata) {
										if (settingdata.authSetting["scope.userLocation"]) {
											uni.showToast({
												title: '获取权限成功'
											});
											self.getNowLocation();
										} else {
											console.log("获取权限失败")
										}
									}
								})
							}
						}
					})
				}
			},

			getLocation() {
				uni.chooseLocation({
					latitude:this.lat1,
					longitude:this.lon1,
					success: (res) => {
						console.log(res);
						this.shopAddress = res.name
						this.lon1 = res.longitude
						this.lat1 = res.latitude;
						
					}
				})
			},

			bindPickerChange: function(e) {
				this.status_index = e.detail.value
				this.status = this.picker_content[e.detail.value].status;
			},
			// 选择店铺类型
			shopChange(e) {
				this.shop_index = e.detail.value;
				this.shopType = this.shop_type[e.detail.value].label;
			},
			// 获取店铺类型
			getType() {
				this.$api.get('/sysDict/list', {
					params: {
						type: 'SHOPTYPE',
						pageno: 1,
						pagesize: 100
					}
				}).then(res => {
					if (res.code && res.code != 200) {
						return
					};
					this.shop_type = res.list;
					if (!this.shopType) {
						this.shopType = this.shop_type[0].label
					}
				})
			},
			// 保存
			save() {
				if (!this.merchantShopName || !this.picUrl || !this.shopAddress || !this.shopDesc || !this.shopManagerMobile || !
					this.shopManagerName || !this.shopType || !this.shopWechatId) {
					this.$box.confirm('请正确输入所有信息', false);
					return
				}
				this.$api.post('/o2oMerchantShop/save', {
					"enterpriseCreditCode": this.enterpriseCreditCode,
					"enterpriseExpireDate": this.enterpriseExpireDate,
					"enterpriseName": this.enterpriseName,
					"merchantSeqId": this.merchantSeqId,
					"merchantShopName": this.merchantShopName, //店铺名称
					"picUrl": this.picUrl,
					"remarks": this.remarks,
					"seqId": this.seqId,
					"shopAddress": this.shopAddress, //店铺地址
					"shopDesc": this.shopDesc, //店铺介绍
					"shopDetailSeqId": this.shopDetailSeqId,
					"shopManagerMobile": this.shopManagerMobile, //店主电话
					"shopManagerName": this.shopManagerName, //店主姓名
					"shopMap": this.shopMap,
					"shopType": this.shopType, //店铺类型
					"shopWechatId": this.shopWechatId, //店长微信
					"status": this.status, //营业状态
					"lon1": this.lon1,
					"lat1": this.lat1,
				}).then(res => {
					if (res.code == 200) {
						// 清除邀请码
						this.$store.commit('saveInviteCode', "")
						uni.setStorageSync('setStatusIndexFunc', 4)
						uni.reLaunch({
							url: '/pages/index'
						})
					}
				}).catch(e => {
					console.log(e);
				})
			},
			// 删除图片
			closeImg(item) {
				this[item] = ""
			},
			// 上传图片
			chooseImage(item) {
				let self = this;
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album'],
					success: (res) => {
						var imageSrc = res.tempFilePaths[0]
						uni.uploadFile({
							url: self.api_url + '/sys/appeal/imgUpload',
							filePath: imageSrc,
							fileType: 'image',
							header: {
								Authorization: self.$common.getLocalSync('token')
							},
							name: 'file',
							success: (res) => {
								if (res.statusCode == 200) {
									let data = JSON.parse(res.data)
									if (data.code == 200) {
										self[item] = data.data.viewUrl;
										self.pic_url = data.data.viewUrl
									} else {
										self.$box.toast('上传失败', 'none')
									}
								} else {
									self.$box.toast('上传失败', 'none')
								}

							},
							fail: (err) => {
								console.log('uploadImage fail', err);
								uni.showModal({
									content: err.errMsg,
									showCancel: false
								});
							}
						});
					},
					fail: (err) => {
						console.log('chooseImage fail', err)
						// #ifdef MP
						uni.getSetting({
							success: (res) => {
								let authStatus = res.authSetting['scope.album'];
								if (!authStatus) {
									uni.showModal({
										title: '授权失败',
										content: '需要从您的相册获取图片，请在设置界面打开相关权限',
										success: (res) => {
											if (res.confirm) {
												uni.openSetting()
											}
										}
									})
								}
							}
						})
						// #endif
					}
				})
			},
		}
	}
</script>

<style lang="scss">
	.box {
		background: white;
		width: 100%;
		min-height: calc(100vh - var(--window-top) - var(--window-bottom));
		padding-bottom: 40rpx;
	}

	.next-btn {
		width: calc(100% - 60rpx);
		background: $color-primary;
		font-size: 30rpx;
		height: 90rpx;
		line-height: 90rpx;
		text-align: center;
		color: white;
		margin: 40rpx auto;
		border-radius: 10rpx;
	}

	.info-li {
		width: calc(100% - 60rpx);
		padding: 0 30rpx;
		min-height: 100rpx;
		align-items: center;
		display: flex;
		justify-content: space-between;
		font-size: 28rpx;
		border-bottom: 1px solid #eee;

		.info-li-input {
			text-align: right;
			color: #000;
		}

		.left {
			display: flex;
			align-items: center;

			.iconfont {
				font-size: 40rpx;
				margin-right: 10rpx;
				flex-shrink: 0;

			}

			.title {
				width: 200rpx;
				flex-shrink: 0;
				font-size: 30rpx;
			}
		}

		.info-li-input {
			flex-grow: 1;
			text-align: right;
			font-size: 28rpx;
			height: 100rpx;
			line-height: 100rpx;
		}

		.info-li-textarea {
			height: 120rpx;
			line-height: 1.5;
		}
	}

	.all-line {
		flex-wrap: wrap;

		.title {
			width: 100%;
			height: 90rpx;
			line-height: 90rpx;
			font-size: 30rpx;
			font-weight: bold;
		}
	}

	.shop-box {
		width: 90rpx;
		height: 90rpx;
		border-radius: 100%;
		background: #eee;
		position: relative;

		.shop-img {
			width: 100%;
			height: 100%;
			border-radius: 100%;
			color: #333;
		}

		.iconfont {
			position: absolute;
			right: 0;
			bottom: 0;
			z-index: 2;
			color: $color-primary;
			font-size: 30rpx;
		}
	}

	.info-photo-li {
		flex-wrap: wrap;

		.card-title {
			width: 100%;
			height: 100rpx;
			line-height: 100rpx;
			font-size: 30rpx;
			font-weight: bold;
		}
	}

	.photo_list {
		width: 100%;
		height: auto;
		display: flex;
		margin-bottom: 40rpx;
		justify-content: space-between;

		.photo_li {
			width: 220rpx;
			height: 200rpx;
			background: #F2F2F2;
			border-radius: 7rpx;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
			position: relative;

			&:last-child {
				margin-right: 0;
			}

			.photo_default {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				justify-content: center;
				cursor: pointer;
			}

			.default_icon {
				width: 53rpx;
				height: 46rpx;
				text-align: center;
				margin: 0 auto;
			}

			.default_img {
				width: 220rpx;
				height: 200rpx;
			}

			.iconcuowu {
				position: absolute;
				top: -16rpx;
				right: -10rpx;
				background: #333;
				border-radius: 100%;
				width: 36rpx;
				height: 36rpx;
				z-index: 1;
				color: white;
				font-size: 30rpx;
				text-align: center;
				line-height: 36rpx
			}

			.default_text {
				width: 100%;
				text-align: center;
				font-size: 24rpx;
				margin-top: 16rpx;
				color: #666;
			}

			.img {
				width: 100%;
				height: 100%;
			}

			.close_icon {
				width: 32rpx;
				height: 32rpx;
				position: absolute;
				top: -10rpx;
				right: -10rpx;
				z-index: 2;
				cursor: pointer;
			}
		}

	}
</style>
